import axios from 'axios'
import config from '../../../../../configs/themeConfig'

export const getAllData = () => {
  return async dispatch => {
    await axios.get(`${config.app.ApiUrl}/brand`).then(response => {
      dispatch({
        type: 'GET_ALL_BRAND_DATA',
        data: response.data.data
      })
      
    })
  }
}
export const create = (data) => {
  return async dispatch => {
    await axios
      .post(`${config.app.ApiUrl}/brand`, data)
      .then(response => {
        
        dispatch({
          type: 'CREATE_BRAND',
          brandList: response.data
        })
        dispatch(getAllData())
      })
      .catch(err => console.log(err))
  }
}
export const upload = (data) => {
  return async dispatch => {
    await axios
      .post(`${config.app.ApiUrl}/brand/upload`, data)
      .then(response => {
        localStorage.setItem('blogo', response.data.data)
        dispatch({
          type: 'LOGO_UPLOAD',
          logo: response.data.data
        })
      })
      .catch(err => console.log(err))
  }
}
export const remove = (id) => {
  return async dispatch => {
    await axios
      .delete(`${config.app.ApiUrl}/brand/${id}`)
      .then(response => {
         dispatch(getAllData())
      })
      .catch(err => console.log(err))
  }
}

export const statusChange = (id, status) => {
  return async (dispatch) => {
    axios
      .patch(`${config.app.ApiUrl}/brand/status/change/${id}`, {
        status: !status
      })
      .then(() => {
        dispatch(getAllData())
      })
      .catch(function (error) {
        dispatch(getAllData())
        console.log(error)
      })
    }
}
