// ** Custom Components
import Sidebar from '@components/sidebar'
import { useDispatch } from 'react-redux'
import { useState } from 'react'
import { Button, FormGroup, Label  } from 'reactstrap'
import { AvForm, AvInput } from 'availity-reactstrap-validation-safe'
import { upload, create } from '../store/action'
const SidebarNewUsers = ({ open, toggleSidebar }) => {
  // ** Function to handle form submit
  const dispatch = useDispatch()
  const [blogo, setLogo] = useState('')
  const onSubmit = (e, errors) => {
    if (!e.length) {
      const { brand } = e.target.elements
      dispatch(create({brand: brand.value, logo: localStorage.getItem('blogo')}))
      toggleSidebar()
    }
    e.preventDefault()
  }
  const changeHandler = (event) => {
    event.preventDefault()
    const formData = new FormData()
    formData.append('logo', event.target.files[0])
    dispatch(upload(formData))
  }

  return (
    <Sidebar
      size='sm'
      open={open}
      title='Brand Information'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <AvForm onSubmit={onSubmit}>
        <FormGroup>
          <Label for='brand'>Brand Name</Label>
          <AvInput name='brand' id='brand' placeholder='brand Name'  required />
        </FormGroup>
        <FormGroup>
          <Label for='branch'>Logo</Label>
          <AvInput name='logo' type="file"  onChange={event => changeHandler(event)} accept="image/png, image/gif, image/jpeg" />
        </FormGroup>
        <Button type='submit' className='mr-1' color='primary'>
          Submit
        </Button>
        <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
          Cancel
        </Button>
      </AvForm>
    </Sidebar>
  )
}

export default SidebarNewUsers
